import styled, { css } from 'styled-components';
import { buttonSize } from '../../styles/ButtonSize';
import { elementSize } from '../../styles/ElementSize';
import { flexElemet } from '../../styles/Flex';
import { space } from '../../styles/Space';
import { common } from '../../styles/Common';
import hexToRGB from '../../modules/hexToRGB';
export const ButtonStyles = styled.button`  
  ${buttonSize}
  transition: all 150ms;
  border-radius: ${({
  theme,
  borderRadius
}) => borderRadius || theme.buttonType === 'box' && '3px' || '1.3125em'}}; 
  cursor: pointer;
  display:${({
  display
}) => display || 'inline-block'};
  text-align: center;
  overflow: hidden;

  ${({
  $loading
}) => $loading && `position:relative;`}
 
  ${({
  type,
  active,
  theme,
  buttonColor,
  size
}) => type === 'outline' && css`
        background-color: white;
        color: ${active && (theme.colors[buttonColor] || buttonColor || theme.colors.primary) || theme.colors.g200};
        border: solid;
        border-width: 1px;
        border-color: ${active && (theme.colors[buttonColor] || buttonColor || theme.colors.primary) || theme.colors.g200};
      ` || type === 'text' && css`
        width: auto;
        background-color: ${active && 'transparent' || theme.colors.g200};
        color: ${active && (theme.colors[buttonColor] || buttonColor || theme.colors.primary) || 'white'};
        ${size === 'none' && 'padding: 8px 10px;'};
      ` || css`
      border: 0;
      background-color: ${active && (theme.colors[buttonColor] || buttonColor || theme.colors.primary) || theme.colors.g200};
      color: white;
    `}

    // active 상태일 때, hover가 가능한 환경에서만 hover animation
    ${({
  active
}) => active && css`
        @media (hover: hover) and (pointer: fine) {
          &:hover {
            opacity: 0.7;
          }
        }
      `}


    &:active { 
      position: relative;
      overflow: hidden;
      &:before {
        position: absolute;
        content: ' ';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%; 
        background-color:${({
  theme
}) => hexToRGB(theme.colors.g900, 0.4)};
      }
    } 
    ${flexElemet} 
    ${common}  
    ${space}
    ${elementSize}
`;