import styled, { css, keyframes } from 'styled-components';
import { common } from '../../styles/Common';
import { borderElement } from '../../styles/Border';
import { flexElemet } from '../../styles/Flex';
import { space } from '../../styles/Space';
import { elementSize } from '../../styles/ElementSize';
import { buttonSize } from '../../styles/ButtonSize';
import hexToRGB from '../../modules/hexToRGB';
const spin = keyframes`
  from { transform: translate(-50%, -50%) rotate(0deg); }
  to { transform: translate(-50%, -50%) rotate(359deg); }`;
const spinButton = keyframes`
  from { transform:rotate(0deg); }
  to { transform: rotate(359deg); }`;
const loader10g = keyframes`
  0%{background-color: rgba(172,172,172, .2);}
  25%{background-color: rgba(172,172,172, 1);}
  50%{background-color: rgba(172,172,172, .2);}
  75%{background-color: rgba(172,172,172, .2);}
  100%{background-color: rgba(172,172,172, .2);}`;
const loader10m = keyframes`
    0%{background-color: rgba(172,172,172, .2);}
    25%{background-color: rgba(172,172,172, .2);}
    50%{background-color: rgba(172,172,172, 1);}
    75%{background-color: rgba(172,172,172, .2);}
    100%{background-color: rgba(172,172,172, .2);}
  `;
const loader10d = keyframes`
  0%{background-color: rgba(172,172,172, .2);}
    25%{background-color: rgba(172,172,172, .2);}
    50%{background-color: rgba(172,172,172, .2);}
    75%{background-color: rgba(172,172,172, 1);}
    100%{background-color: rgba(172,172,172, .2);}`;
export const LoadingStyles = styled.div`
  ${common}
  ${space}
  ${flexElemet}
  ${borderElement}
  ${elementSize}
  z-index: 9999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  ${({
  type,
  theme
}) => type === 'inside' && css`
        position: absolute;
        content: ' ';
        backdrop-filter: blur(2px);
        background-color: ${hexToRGB('#ffffff', 0.6)};

        & > .loader {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);

          width: 12px;
          height: 12px;
          border-radius: 12px;
          -webkit-animation: ${loader10m} 1.5s ease-in-out infinite;
          animation: ${loader10m} 1.5s ease-in-out infinite;

          &:before {
            content: '';
            position: absolute;
            top: 0px;
            left: -25px;
            height: 12px;
            width: 12px;
            border-radius: 12px;
            -webkit-animation: ${loader10g} 1.5s ease-in-out infinite;
            animation: ${loader10g} 1.5s ease-in-out infinite;
          }

          &:after {
            content: '';
            position: absolute;
            top: 0px;
            left: 25px;
            height: 10px;
            width: 10px;
            border-radius: 10px;
            -webkit-animation: ${loader10d} 1.5s ease-in-out infinite;
            animation: ${loader10d} 1.5s ease-in-out infinite;
          }
        }
      ` || type === 'button' && css`
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: transparent;
        ${buttonSize}

        & > .loader {
          box-sizing: border-box;
          margin: 0 auto;
          aspect-ratio: 1 / 1;
          height: 100%;

          border: 2.5px solid ${theme.colors.primary};
          border-right-color: white;
          border-top-color: white;
          border-radius: 100%;
          animation: ${spinButton} 900ms infinite linear;
        }
      ` || css`
      position: fixed;
      backdrop-filter: blur(2px);
      background-color: ${hexToRGB('g900', 0.6)};

      & > .loader {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        width: 60px;
        height: 60px;
        border: 6px solid ${theme.colors.primary};
        border-right-color: white;
        border-top-color: white;
        border-radius: 100%;
        animation: ${spin} 900ms infinite linear;
      }
    `}
`;